@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700;800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300;400;500;700&display=swap);
:root {
  --text-four: 'Noto Sans KR', sans-serif;
}
body {
  width: 100%;
  max-width: 100em;
  margin: 0 auto; /* Center alignment */
  font-family: 'Montserrat', sans-serif;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Page Best*/
.product__best {
  display:inline-block;
  background-image: url(/what-happened-market/static/media/bes-bg.08b8b84f.png);
  background-repeat: no-repeat;
  background-size: cover;
  height: 60rem;
  text-align: center;
  width: 100%;
}

.title__best {
  color: #f96400;
  margin: 7rem 0 0 0;
  margin: auto;
  margin-top: 7rem;
  width: 40%;
  font-weight: bold;
}

/* best_item */
.item__best {
  display: inline-flex;
  width: 80%;
  margin-top: 2.5rem
}

/* End bestitem */

/* End Page Best */

.main-banner {
    display: flex;
    background-image: url(/what-happened-market/static/media/main-banner.b48c79d1.png);
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    min-height: 52rem;

}
.nav {  
    background: #1f1f1f;  
    padding: 0 2rem;
}
.row{
    display: flex;
    justify-content: space-between;
}
.top-bar__links{
    display: flex;
    align-items: center;
    padding: 1.2rem 0;
    margin: 0;
    list-style-type: none;
}
.top-bar__links > li > a,
.top-bar__links > li > p{
    color: white;
    margin: 0 1rem;
    text-decoration: none;
}
.col-logo {
    padding: 1rem 0;
    text-align: center;
}
  

.banner {
 
    display: flex;
    background-image: url(/what-happened-market/static/media/introduction-bg.1563ce83.png);
    background-repeat: no-repeat;
    background-size: cover;
    height: 29rem;
    object-fit: contain;
}

.content {
    margin: 5.7rem 0 8rem 8.6rem;
    height: 8rem;
    width: 36%;
    color: #fff;
}

.title {
    font-weight: 800;
    font-size: 2.5rem;
    letter-spacing: 0.3rem;
    margin-bottom: 1.5rem;
}

.description {
    font-size: 1rem;
    line-height: 1.75;
}

.description2 {
    font-family: var(--text-four);
}
.footer {
    margin: 6rem 0 3rem;
    align-items: center;
}
.line {
    height: 1px;
    background-color: #f4f4f4;
    margin: 0 6rem;
}

.title1 {
    font-size: 10px;
    font-weight: 600;
    color: #111111;
    margin-bottom: 0.5rem;
    line-height: 2.86;
}


.footer-content {
    margin: 1.5rem 6rem 3rem;
    display: flex;
    justify-content: space-between;
}

.footer-content  div  a {
    text-decoration: none;
    font-size: 10px;
    font-weight: 300;
    color: #333333;
    line-height: 2.21;
    font-family: var(--text-four);
}

.footer-content-left {
    display: flex;
    flex-direction: column;
}

.footer-content-link {
    display: flex;
    grid-gap: 6rem;
    gap: 6rem;
}
.footer-content-link-column {
    display: flex;
    flex-direction: column;
}

.footer-copyright {
    display: flex;
    justify-content: space-between;
    margin: 1rem 6rem 0rem 6rem;
}

.footer-copyright  div  a {
    text-decoration: none;
    font-size: 10px;
    font-weight: 300;
    color: #a9a9a9;
    line-height: 1.71;
    font-family: var(--text-four);
}

.footer-icon {
    display: flex;
    align-items: center;
}

.Icon {
 
    margin-left: 0.8rem;
}
